.loader {
  content: " ";
  border: 0.2em solid #626262;
  width: 4em;
  height: 4em;
  border-radius: 4em;
  border-top-color: #b6b7b5;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
